.cube {
  --width: 55vmin;
  --height: 50vmin;
  --depth: 1vmin;
  height: var(--height);
  width: var(--width);
  position: relative;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-animation: cuberotation 12s infinite linear alternate;
          animation: cuberotation 12s infinite linear alternate;
}

@-webkit-keyframes cuberotation {
  to {
    -webkit-transform: translateY(-8vmin)rotateY(360deg);
            transform: translateY(-8vmin)rotateY(360deg);
  }
}

@keyframes cuberotation {
  to {
    -webkit-transform: translateY(-8vmin)rotateY(360deg);
            transform: translateY(-8vmin)rotateY(360deg);
  }
}

.cube_side {
  background-color: hsla(0,0%,100%,0.2);
/*   border:1px solid black; */
  position:absolute;
}

.cube_side:nth-of-type(1),
.cube_side:nth-of-type(2) {
  --coefficient: 0.5;
  height: var(--height);
  width: var(--width);
  -webkit-transform: translate3d(0,0,calc(var(--depth) * var(--coefficient)));
          transform: translate3d(0,0,calc(var(--depth) * var(--coefficient)));
  -webkit-filter: brightness(100%);
          filter: brightness(100%);
}

.cube_side:nth-of-type(2){
  --coefficient: -0.5;
  -webkit-filter: brightness(75%);
          filter: brightness(75%);
}

.cube_side:nth-of-type(3),
.cube_side:nth-of-type(4){
  --rotation: 90deg;
  height: var(--height);
  width: var(--depth);
  background-color:black;
  left:50%;
  top:50%;
  -webkit-transform:translate(-50%, -50%)rotateY(var(--rotation)) translate3d(0,0, calc(var(--width) * -0.5));
          transform:translate(-50%, -50%)rotateY(var(--rotation)) translate3d(0,0, calc(var(--width) * -0.5));
  
}

.cube_side:nth-of-type(4) {
  background-color: grey;
  --rotation: -90deg;
}

.cube_side:nth-of-type(5), 
.cube_side:nth-of-type(6) {
  --rotation: -90deg;
  height: var(--depth);
  width: var(--width);
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%) rotateX(var(--rotation)) translate3d(0,0,calc(var(--height) * -0.5));
          transform: translate(-50%, -50%) rotateX(var(--rotation)) translate3d(0,0,calc(var(--height) * -0.5));
}

.cube_side:nth-of-type(6){
  --rotation: 90deg;
}

